.navbar_container {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  background: #12343b;
  color: white;
  font-size: 30px;

  span {
    font-size: 50px;
  }

  a {
    display: flex;
    justify-content: start;
    align-items: center;
    width: 154px;
    height: 40px;
    text-decoration: none;
    color: white;
    text-align: left;

    a:active {
      color: blue;
    }
  }
}
